.tax-software-section-1 {
    .img-bg img {
        box-shadow: 0px 30px 60px 0px rgba(0, 0, 0, 0.1);
    }
}

.tax-software-section-5 {
    // accordion
    .accordion {
        .card {
            background: transparent;
            transition: all 0.3s linear;

            .card-header {
                background: transparent;

                a {
                    border-top-left-radius: 10px;
                    border-top-right-radius: 10px;
                    background-color: var(--tc-system-white);
                    transition: all 0.3s ease-in-out;

                    .arrow {
                        &::before {
                            transition: 0.3s;
                            transform: rotate(0deg);
                            font-family: remixicon;
                            content: '\f1af';
                            color: var(--tc-theme-primary);
                            font-size: 14px;
                            font-weight: 300;
                        }
                    }

                    &.collapsed {
                        background-color: transparent;

                        .arrow {
                            background-color: transparent;

                            &::before {
                                transition: 0.3s;
                                color: var(--tc-system-black);
                                transform: rotate(180deg);
                                content: '\ea13';
                            }
                        }
                    }
                }
            }
            .collapse {
                background: linear-gradient(180deg, #fff 0%, #eff6ff 113.64%);
                transition: all 0.3s linear;
                &.show {
                    border-bottom: 0;
                }
            }
            .collapsing {
                background: linear-gradient(180deg, #fff 0%, #eff6ff 113.64%) !important;
                transition: all 0.3s linear;
            }
        }
    }
}

.tax-software-section-7 {
    .logo-top {
        margin-bottom: 140px;

        .ellipse-1 {
            width: 120px;
            height: 120px;
            border-radius: 120px;
            border: 1px solid var(--tc-system-stroke);
        }

        .ellipse-2 {
            width: 220px;
            height: 220px;
            border-radius: 220px;
            border: 1px solid var(--tc-system-stroke);
            opacity: 0.6;
        }

        .ellipse-3 {
            width: 328px;
            height: 328px;
            border-radius: 328px;
            border: 1px solid var(--tc-system-stroke);
            opacity: 0.3;
        }
    }
}

.section-footer-7 {
    .bg-linear {
        background: linear-gradient(180deg, #eff6ff 0%, #fff 100.3%);
    }

    .icon {
        svg {
            height: 14px;
            width: 14px;
            fill: var(--tc-body-text-secondary);
            opacity: 0.5;
        }

        &:hover {
            svg {
                fill: var(--tc-system-black);
                opacity: 1;
                transition-duration: 0.3s;
            }
        }
    }
}

.card-pricing {
    .sale-off {
        border-radius: 0px 0px 6px 6px;
        border: 1px solid var(--tc-theme-primary);
        border-top: 0;
        background: rgba(121, 74, 255, 0.1);
    }
}

.change-price-plan {
    .btn-switch {
        width: 64px;
        height: 32px;
        border-radius: 15px;
        background: var(--tc-theme-primary);

        .switch {
            width: 18px;
            height: 18px;
            border-radius: 20px;
            background: var(--tc-system-white);
            position: absolute;
            top: 50%;
            left: 8%;
            transform: translateY(-50%);
        }
    }

    .monthly,
    .yearly {
        &.active {
            background: transparent !important;
            color: var(--tc-theme-primary) !important;
            transition: 0.3s;
        }
    }
}

.card-contact {
    .icon-60 {
        transition: all 0.3s linear;
    }

    &:hover {
        background-color: var(--tc-theme-primary) !important;

        .icon-60 {
            background-color: var(--tc-system-white) !important;

            svg {
                path {
                    stroke: var(--tc-theme-primary);
                }
            }
        }

        .card-contact-content {
            h6 {
                color: var(--tc-system-white);
            }

            p {
                color: var(--tc-system-white);
            }
        }
    }
}

.form-group {
    .form-control {
        color: var(--tc-system-black);
        border-radius: 8px;
        background: white;

        &::placeholder {
            color: var(--tc-system-black);
        }
    }

    textarea {
        min-height: 140px;
    }

    .form-select {
        background-color: white;
        border-radius: 8px;

        &:focus {
            outline: none;
            box-shadow: none;
        }
    }
}

.tax-software-contact-section-1 {
    .contact-map {
        max-height: 440px;
        position: relative;
    }
}

.pricing__tab {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin-bottom: 40px;
}

.pricing__tab-btn {
    cursor: pointer;
    color: var(--tc-system-black);
    font-weight: 400;
    font-size: 18px;
    user-select: none;
    -webkit-transition: all 0.3s ease-out 0s;
    -moz-transition: all 0.3s ease-out 0s;
    -ms-transition: all 0.3s ease-out 0s;
    -o-transition: all 0.3s ease-out 0s;
    transition: all 0.3s ease-out 0s;
}

.pricing__tab-switcher {
    height: 30px;
    width: 60px;
    display: inline-block;
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
    -o-border-radius: 30px;
    -ms-border-radius: 30px;
    border-radius: 30px;
    position: relative;
    margin: 0 15px;
    -webkit-transform: translateY(0px);
    -ms-transform: translateY(0px);
    transform: translateY(0px);
    cursor: pointer;
    border: 1px solid var(--tc-theme-primary);
    background: var(--tc-theme-primary);
}

.pricing__tab-switcher::before {
    content: '';
    position: absolute;
    left: 2px;
    top: 2px;
    bottom: 2px;
    width: 26px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -o-border-radius: 50%;
    -ms-border-radius: 50%;
    border-radius: 50%;
    background: var(--tc-system-white);
    -webkit-transition: all 0.3s ease-out 0s;
    -moz-transition: all 0.3s ease-out 0s;
    -ms-transition: all 0.3s ease-out 0s;
    -o-transition: all 0.3s ease-out 0s;
    transition: all 0.3s ease-out 0s;
}

.pricing__tab-switcher.active:before {
    left: calc(100% - 28px);
}

.pricing__box {
    background: #fff;
    border: 1px solid #cfdde2;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -o-border-radius: 15px;
    -ms-border-radius: 15px;
    border-radius: 15px;
    padding: 50px 65px;
    margin-bottom: 30px;
}

.pricing__price .price.annual_price {
    display: none;
}

.pricing__price.change-subs-duration .monthly_price {
    display: none;
}

.pricing__price.change-subs-duration .annual_price {
    display: flex;
}
